import dbConnect from "@/lib/mongodb";
import { BankSetting } from "@/lib/models"; // single-file Mongoose models
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { BankSettingsForm } from "@/components/admin/bank-settings-form";
import { Settings, Percent } from "lucide-react";

export default async function AdminSettingsPage() {
  await dbConnect(); // connect to MongoDB

  // Fetch all bank settings sorted by settingKey
  const settings = await BankSetting.find({}).sort({ settingKey: 1 }).lean();

  const interestSettings =
    settings.filter((s) => s.settingKey.endsWith("_loan_rate")) || [];
  const otherSettings =
    settings.filter((s) => !s.settingKey.endsWith("_loan_rate")) || [];

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-2xl font-bold text-foreground">System Settings</h1>
        <p className="text-muted-foreground">
          Manage bank policies and interest rates
        </p>
      </div>

      <div className="grid gap-6 lg:grid-cols-2">
        {/* Interest Rates */}
        <Card className="border-0 shadow-sm">
          <CardHeader>
            <div className="flex items-center gap-3">
              <div className="flex h-10 w-10 items-center justify-center rounded-lg bg-primary/10">
                <Percent className="h-5 w-5 text-primary" />
              </div>
              <div>
                <CardTitle>Interest Rates</CardTitle>
                <CardDescription>Configure loan interest rates</CardDescription>
              </div>
            </div>
          </CardHeader>
          <CardContent>
            <BankSettingsForm settings={interestSettings} />
          </CardContent>
        </Card>

        {/* Other Settings */}
        <Card className="border-0 shadow-sm">
          <CardHeader>
            <div className="flex items-center gap-3">
              <div className="flex h-10 w-10 items-center justify-center rounded-lg bg-accent/10">
                <Settings className="h-5 w-5 text-accent" />
              </div>
              <div>
                <CardTitle>Bank Policies</CardTitle>
                <CardDescription>Configure system policies</CardDescription>
              </div>
            </div>
          </CardHeader>
          <CardContent>
            <BankSettingsForm settings={otherSettings} />
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
